button, .button, select, .select {
	border-radius: 4px;
	height: 50px;
	padding: 0 15px;
	display: inline-flex;
	justify-content: space-between;
	align-items: center;
	font-weight: 700;
	box-shadow: 0 2px 2px 0 rgba(0,0,0,0.10);
	background-image: linear-gradient(-180deg, #FFFFFF 90%, $lightgrey 99%);
	border: 1px solid $grey-95;
	color: $orange;
	margin-bottom: 1px;
	margin-top: 0;
	box-sizing: border-box;

	&.small {
		height: 30px;
	}

	&.transparent {
		background-image: none;
		box-shadow: none;
		background-color: transparent;
		outline: 0;
		border: 0;
	}

	&.close {
		padding: 0;
		width: 30px;
		@extend %fa-icon;
		@extend .fas;
		display: flex;
		align-items: center;
		justify-content: center;

		&:before {
			content: fa-content('\f00d');
		}
	}
	&:hover:not(.transparent) {
		border: 1px solid $grey-90;
	}
	&:active:not(.transparent) {
		background-image: none;
		background-color: white;
		box-shadow: 0 0 0 0 rgba(0,0,0,0.00);
		margin-top: 1px;
		margin-bottom: 0;
	}
	&:focus {
		outline: 0;
	}
}
// orange
button.orange, .button.orange {
	background-image: linear-gradient(-180deg, $orange 90%, darken($orange, 2%) 100%);
	color: white;
	border: 1px solid $orange;
	outline: 0;

	&:hover {
		border: 1px solid darken($orange, 5%);
		background-image: linear-gradient(-180deg, darken($orange, 2%) 90%, darken($orange, 4%) 100%);
	}
	&:active {
		background-image: none;
		background-color: darken($orange, 2%);
		box-shadow: 0 0 0 0 rgba(0,0,0,0.00);
		border: 1px solid $orange;
		margin-top: 1px;
		margin-bottom: 0;
	}
}

button.zoom:hover, .button.zoom:hover {
	transform: scale(1.1);
}

.button-height {
	height: 50px;
	display: flex;
	align-items: center;
}

.form-group {
	display: flex;
	label {
		padding-top: 15px;
	}
	small {
		display: block;
		width: 100%;
		&.error {
			color: $red;
		}
		&.success {
			color: $green;
		}
	}
}

label {
	font-weight: 700;
	margin-right: 10px;
}

input {
	border-radius: 4px;
	height: 50px;
	padding: 0 15px;
	display: inline-flex;
	justify-content: space-between;
	align-items: center;
	box-shadow: 0 2px 2px 0 rgba(0,0,0,0.02);
	background-image: linear-gradient(-180deg, $lightgrey 0%, #FFFFFF 10%);
	border: 1px solid $grey-95;
	color: $grey;
	margin-bottom: 1px;
	margin-top: 0;

	&:focus {
		outline: 0;
		border: 1px solid $grey-80;
	}
}

.internal-pagination {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	a {
		@extend .button;
		@extend .button.zoom;
	}
	&.orange {
		a {
			@extend .button.orange;
			@extend .button.zoom;
		}
	}
	a {
		padding: 0;
		display: flex;
		justify-content: center;
		margin-right: 5px;
		margin-bottom: 5px;
		width: 20px;
		height: 20px;
	}
}

.dropdown-menu {
	.dropdown-item {
		padding: 5px 15px;
		width: auto;
	}
}
